  height: 5rem;
  object-fit: cover;
  border-radius: 50%;
  border: 0.1rem solid #d8dde3;
}
.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}
.table__accion--editar {
  color: #007df4;
}
.table__accion--eliminar {
  color: rgb(169, 0, 0);
}
.table__accion:hover {
  cursor: pointer;
}

.paginacion {
  margin-top: 5rem;
  margin-bottom: 3rem;
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .paginacion {
    justify-content: flex-end;
  }
}
.paginacion__enlace {
  color: #007df4;
  font-size: 1.6rem;
}
.paginacion__enlace--texto {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #007df4;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.paginacion__enlace--texto:hover {
  background-color: #007df4;
  color: #FFFFFF;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.paginacion__enlace--numero {
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--numero {
    display: inline;
  }
}
.paginacion__enlace--actual {
  color: #64748B;
  font-weight: 700;
  display: none;
}
@media (min-width: 768px) {
  .paginacion__enlace--actual {
    display: inline;
  }
}

.listado-ponentes {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(1, 1fr);
}
.listado-ponentes__ponente, .listado-ponentes__ponente--seleccionado {
  color: #007df4;
  border: 0.1rem solid #007df4;
  padding: 1rem;
  border-radius: 1rem;
  text-align: center;
}
.listado-ponentes__ponente:hover, .listado-ponentes__ponente--seleccionado:hover {
  cursor: pointer;
}
.listado-ponentes__ponente--seleccionado {
  background-color: #007df4;
  color: #FFFFFF;
}