@use 'base/variables' as v;
@use 'base/mixins' as m;

.navegacion{

    display: flex;
    flex-direction: column;

    @include m.desktop {
        flex-direction: row;
    }

    &__enlace{
        color: v.$blanco;
        font-size: 2.2rem;
        padding: 2rem;
        font-weight: v.$black;
        text-transform: uppercase;
        text-align: center;
        transition: color .3s ease, background-color .3s ease;

        &:hover{
            background-color: v.$blanco;
            color: v.$azul;

            transition: color .3s ease, background-color .3s ease;
        }
    }

}